<template>
  <div class="goodMain">
    <div class="dromNum">
            寝室号:{{dromNum}}
    </div>
    <div class="des-606"></div>
  <div class="goods">
    <div class="menu-wrapper menu-list-hook" ref="menuWrapper">
      <ul>
        <li v-for="(item,index) in goods" :key="index" class="menu-item"
            :class="{'current': currentIndex === index}" @click="selectMenu(index,$event)">
          <span class="text border-1px">
               {{item.name}}
           </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="(item,index) in goods" :key="index" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li @click="selectFood(food,$event)" v-for="(food,index) in item.foods" class="food-item border-1px" :key="index">
              <div class="icon">
                <img width="68" height="68" :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <div class="extra">
                  <span class="count">库存{{food.stock}}份</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}元</span><span class="old"
                                                                 v-show="food.oldPrice">￥{{food.oldPrice}}元</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food" @add="addFood"></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart ref="shopcart" :select-foods="selectFoods" ></shopcart>
    <food @add="addFood" :food="selectedFood" ref="food"></food>
  </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';
  import Cartcontrol from '../cartcontrol/cartcontrol';
  import shopcart from '../shopcart/shopcart';
  import food from '../food/food';
  import axios from 'axios';
  var config = require('../../../../config');
  config = process.env.NODE_ENV === 'development' ? config.dev : config.build;
  // const ERR_OK = 0;
  export default {
    data() {
      return {
        goods: [],
        listHeight: [],
        scrollY: 0,
        selectedFood: {},
        dromNum: ''
      };
    },
    computed: {
      currentIndex() {
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            return i;
          }
        }
        return 0;
      },
      selectFoods() {
        let foods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              foods.push(food);
            }
          });
        });
        return foods;
      }
    },
    mounted() {
      // 监听物理返回按钮
      let that = this;
      that.pushHistory();
      window.addEventListener('popstate', function(e) {
        window.location.href = config.buyerUrl + '/buyerInterface/order.html';
      }, false);
    },
    created() {
      // http://chibei.s1.natapp.cc/sell/wechat/authorize2token?returnUrl=http://it7785.natappfree.cc/page/order/index.html
      if (window.history.length === 1) { // 判断是第一次从微信菜单进入页面
        this.pushHistory(); // 写入空白历史记录
      }
      var token = this.getCookie('token'); // 从cookie得到Token
      var token1 = this.getQueryToken('token'); // 从url得到token
      if (token === null && token1 === null) {
        window.location.href = config.tokenUrl + '?returnUrl=' + config.buyerUrl + '/buyerInterface/order.html';
        return;
      } else if (token1 !== null) {
        this.setTime(token1); // 设置缓冲时间
        token = token1;
      }
      axios.post('/sell/token/verify', {token: token}, {
        headers: {
            'Content-Type': 'application/json'
          }
        })
          .then(function (response) {
            console.log(response.data.code);
            if (response.data.code === 40) {
              window.location.href = config.tokenUrl + '?returnUrl=' + config.buyerUrl + '/buyerInterface/order.html';
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      console.log(token);
      axios.get('/sell/group/product/list?token=' + token).then((response) => {
          console.log(response);
          this.dromNum = response.data.msg;
          if (this.dromNum === '用户不存在') {
            window.alert('请先申请盒子注册用户');
            this.dromNum = '未知';
          } else {
            response = response.request.responseText;
            var obj = JSON.parse(response).data;
            this.goods = obj;
            this.$nextTick(() => {
              this._initScroll();
              this._calculateHeight();
            });
          }
      }).catch(function (error) {
        console.log(error);
      });
    },
    methods: {
      // 写入空白历史记录
      pushHistory() {
        var state = {
          title: 'title',
          url: '#'
        };
        window.history.pushState(state, 'title', '#');
     },
      // 从缓冲中获取token
      getCookie(name) {
        var arr = document.cookie.match(new RegExp('(^| )' + name + '=([^;]*)(;|$)'));
        if (arr != null) {
            return unescape(arr[2]);
          } else {
            return null;
          }
      },
      // 从URL得到token
      getQueryToken(name) {
        var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
        var r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]);
        return null;
      },
      // 设置token缓冲时间
      setTime(token) {
        var date = new Date();
        date.setTime(date.getTime() + 120 * 60 * 1000); // 设置date为当前时间+30分
        document.cookie = 'token=' + token + ';expires=' + date.toGMTString(); // 将date赋值给token
      },
      addFood(target) {
        this._drop(target);
      },
      selectMenu(index, event) {
        if (!event._constructed) {
          return;
        }
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
        let el = foodList[index];
        this.foodsScroll.scrollToElement(el, 300);
      },
      selectFood(food, event) {
        if (!event._constructed) {
          return;
        }
        this.selectedFood = food;
        this.$refs.food.show();
      },
      _initScroll() {
        this.meunScroll = new BScroll(this.$refs.menuWrapper, {
          click: true
        });
        this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
          probeType: 3,
          click: true
        });
        this.foodsScroll.on('scroll', (pos) => {
          // 判断滑动方向，避免下拉时分类高亮错误（如第一分类商品数量为1时，下拉使得第二分类高亮）
          if (pos.y <= 0) {
            this.scrollY = Math.abs(Math.round(pos.y));
          }
        });
      },
      _calculateHeight() {
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
        let height = 0;
        this.listHeight.push(height);
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i];
          height += item.clientHeight;
          this.listHeight.push(height);
        }
      },
      _drop(target) {
        this.$nextTick(() => {
          this.$refs.shopcart.drop(target);
        });
      }
    },
    components: {
      Cartcontrol,
      shopcart,
      food
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../../common/stylus/mixin.styl";
  .goodMain
    background: #BABABA
    .dromNum
      display: inline
      height: 20px
      width: 100%
      font-size : 20px
      padding: 2px 0px 0px 10px
    .des-606
      display: inline
      float: right
      font-size: 8px
      padding: 9px 5px 0px 10px
  .goods
    display: flex
    position: absolute
    top: 20px
    bottom: 45px
    width: 100%
    overflow: hidden
    .menu-wrapper
      flex: 0 0 80px
      width: 80px
      background: #f3f5f7
      .menu-item
        display: table
        height: 54px
        width: 56px
        line-height: 14px
        padding: 0 12px
        background: #E8E8E8
        border-1px(rgba(139,137,137,0.5))
        &.current
          position: relative
          border-left:1px solid #00F
          background: rgb(255,255,255)
          .text
            font-weight: bold
        .text
          display: table-cell
          width: 56px
          vertical-align: middle
          font-size: 12px
    .foods-wrapper
      flex: 1
      .title
        padding-left: 14px
        height: 26px
        line-height: 26px
        border-left: 2px solid #d9dde1
        font-size: 12px
        color: rgb(147,153,159)
        background: #f3f5f7
      .food-item
        display: flex
        margin: 10px
        padding-bottom: 10px
        border-1px(rgba(7,17,27,0.5))
        &:last-child
          broder-none()
          margin-bottom: 0
        .content
          flex: 1
          .name
            margin: 2px 0 8px 0
            height: 14px
            line-height: 14px
            font-size: 14px
            color: rgb(7,17,27)
          .desc,.extra
            line-height: 10px
            font-size: 10px
            color: rgb(147,153,159)
          .desc
            margin-bottom: 8px
          .extra
            .count
              margin-right: 12px
          .price
            font-weight: 700
            line-height: 24px
            .now
              margin-right: 8px
              font-size: 14px
              color: rgb(240,20,20)
            .old
              text-decoration: line-through
              font-size: 10px
              color: rgb(147,153,159)
          .cartcontrol-wrapper
            position: absolute
            right: 0
            bottom : 12px
</style>



